.button {
    margin: 10px;
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    padding: 12px 14px 12px 24px;
    border: 1px solid #488a74;
    border-radius: 8px;
    background: -webkit-gradient(linear, left top, left bottom, from(#75e1bd), to(#488a74));
    background: #75e1bd;
    background: -webkit-linear-gradient(top, #75e1bd, #488a74);
    background: linear-gradient(to bottom, #75e1bd, #488a74);
    text-shadow: #2d5749 1px 1px 1px;
    font: normal normal 20px arial;
    color: #ffffff;
    text-decoration: none;
    font-family: 'Spicy Rice', cursive;
}
.button:hover,
.button:focus {
    border: 1px solid #5aad91;
    background: #8cffe3;
    background: -webkit-gradient(linear, left top, left bottom, from(#8cffe3), to(#56a68b));
    background: -webkit-linear-gradient(top, #8cffe3, #56a68b);
    background: linear-gradient(to bottom, #8cffe3, #56a68b);
    color: #ffffff;
    text-decoration: none;
}
.button:active {
    background: #488a74;
    background: -webkit-gradient(linear, left top, left bottom, from(#488a74), to(#488a74));
    background: -webkit-linear-gradient(top, #488a74, #488a74);
    background: linear-gradient(to bottom, #488a74, #488a74);
}
.button:before{
    content:  "\A0";
    display: inline-block;
    height: 24px;
    width: 24px;
    line-height: 24px;
    margin: 0 4px -6px -4px;
    position: relative;
    top: 0px;
    left: 0px;
   background-size: 100% 100%;
}





